.card-gift {
  display: flex;
  flex-direction: column;

  padding: 20px 0 10px;
  width: 190px;
  height: 100%;

  border: 0.5px solid $color-gray;
  border-radius: 2px;

  @media (max-width: $viewport--desktop) {
    width: 100%;
  }

  @media (max-width: $viewport--tablet) {
    padding: 10px 0 8px;
  }
}

.card-gift__inner {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-grow: 1;

  margin-bottom: 20px;
  width: 100%;
}

.card-gift__img-wrap {
  margin-bottom: 5px;

  @media (max-width: $viewport--tablet) {
    margin-bottom: 7px;
  }
}

.card-gift__info {
  margin: 0 auto 8px;
  padding: 8px 5px;
  width: 138px;

  font-weight: 600;

  text-align: center;
  letter-spacing: 0;
  text-transform: none;

  color: $color-default-white;
  border-radius: 51px;
  background-color: $color-default-black;

  @media (max-width: $viewport--tablet) {
    margin-left: 10px;
    padding: 0 5px 0;
    width: 70%;

    font-size: 10px;
    line-height: 11px;
  }

  @media (max-width: $viewport--sm) {
    width: 75%;
  }
}

.card-gift__img-wrap img {
  margin: 0 auto;
  width: 171px;
  height: 164px;

  @media (max-width: $viewport--tablet) {
    width: 92px;
    height: 89px;
  }
}

.gift .card-gift__name {
  padding: 0 15px;
  min-height: 70px;

  @media (max-width: $viewport--tablet) {
    padding: 0 9px;
    min-height: 40px;
  }

  @media (max-width: $viewport--sm) {
    padding: 0 4px;
  }
}

.gift .card-gift__name p {
  display: block;

  margin: 0;
  padding-left: 0;

  font-size: 18px;
  font-weight: 600;
  line-height: 1.3;

  letter-spacing: 0.03em;

  @media (max-width: $viewport--tablet) {
    margin-left: 0;
    padding: 0 2px 0 0;

    font-size: 10px;
    line-height: 12px;

    letter-spacing: 0.3px;
    text-transform: none;
  }

  &--bottle {
    padding-right: 60px;

    @media (max-width: $viewport--tablet) {
      padding-right: 30px;
    }
  }
}

.card-gift__name span {
  display: block;

  margin: 0;

  font-size: 16px;
  font-weight: 400;

  letter-spacing: -0.03em;

  opacity: 0.5;

  @media (max-width: $viewport--tablet) {
    margin: 0;

    font-size: 12px;
  }
}

.is-gift-active .card-gift {
  border: 0;
}
