.checkbox__item label {
  position: relative;

  display: inline-block;

  padding-left: 25px;

  font-size: 10px;
  line-height: 1.5;

  cursor: pointer;
  vertical-align: top;

  @media (max-width: $viewport--tablet) {
    margin-left: 18px;
  }
}

.form-order__checkbox-wrap .checkbox__item:first-of-type {
  margin-bottom: 25px;

  @media (max-width: $viewport--tablet) {
    order: 1;

    margin-bottom: 0;
  }
}

.form-order__checkbox-wrap .checkbox__item:first-of-type label {
  font-size: 14px;

  @media (max-width: $viewport--tablet) {
    font-size: 10px;

    letter-spacing: 0.02em;
  }
}

.form-order__checkbox-wrap .checkbox__item:last-of-type {
  width: 305px;

  @media (max-width: $viewport--desktop) {
    width: 500px;
  }

  @media (max-width: $viewport--tablet) {
    margin-bottom: 26px;
    width: auto;
  }
}

.form-order__checkbox-wrap .checkbox__item:last-of-type label {
  text-decoration: underline;

  @media (max-width: $viewport--tablet) {
    line-height: 1.4;

    letter-spacing: 0.02em;
  }

  &:hover {
    text-decoration: none;
  }
}

.checkbox__item input + span::before,
.checkbox__item input + span::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;

  width: 13px;
  height: 13px;

  transform: translateY(-50%);
}

.checkbox__item input + span::before {
  background-color: $color-default-black;
}

.checkbox__item input:checked + span::after {
  top: 4px;
  left: 2px;

  width: 9px;
  height: 5px;

  transform: rotate(-45deg);

  border-bottom: 2px solid $color-default-white;
  border-left: 2px solid $color-default-white;
}

.checkbox__item input:focus + span::before {
  opacity: 0.7;
}

.checkbox__item:first-of-type input + span::before {
  border: 1px solid $color-default-black;
  background-color: transparent;
}

.checkbox__item:first-of-type input:checked + span::after {
  top: 7px;
  left: 2px;

  border-color: $color-default-black;

  @media (max-width: $viewport--tablet) {
    top: 4px;
  }
}

.checkbox__item:last-of-type input + span::before {
  top: 3px;

  width: 10px;
  height: 10px;

  transform: none;

  @media (max-width: $viewport--tablet) {
    width: 13px;
    height: 13px;
  }
}

.checkbox__item:last-of-type input:checked + span::after {
  top: 5px;
  left: 1px;

  width: 8px;
  height: 4px;

  @media (max-width: $viewport--tablet) {
    top: 7px;
    left: 3px;

    width: 7px;
    height: 4px;
  }
}

.form-order__item .checkbox {
  @media (max-width: $viewport--tablet) {
    display: flex;
    flex-direction: column;

    width: 100%;
  }
}

.form-order__item--checkbox .checkbox__item label {
  @media (max-width: $viewport--tablet) {
    margin-left: 17px;
  }
}
