.category {
  display: flex;
  justify-content: flex-start;
  align-items: center;

  min-height: 94px;
  padding: 28px;

  font-family: $font-family--open-sans;
  font-weight: 700;
  font-size: 16px;
  line-height: 120%;
  letter-spacing: 0.01em;

  & + & {
    margin-top: 12px;
  }

  &--collection {
    position: relative;

    flex-direction: column;
    justify-content: flex-end;

    min-height: 164px;
    padding-bottom: 10px;

    font-size: 38px;

    text-transform: uppercase;

    color: $color-default-white;

    background-color: $color-lightgray;
    background-image: url("/img/bg/category-collection@1x.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 376px auto;

    .category__logo {
      margin-top: 15px;
    }

    // &::before {
    //   position: absolute;
    //   top: 0;
    //   bottom: 0;
    //   left: 0;
    //   right: 0;

    //   // background-color: rgba(180, 178, 178, 0.9);
    //   // background-color: rgba(0, 0, 0, 0.1);

    //   content: "";
    // }
  }

  &--print {
    background-color: $color-very-lightgreen;
    background-image: url("/img/bg/category-print@1x.jpg");
    background-repeat: no-repeat;
    background-position: center right;
  }

  &--no-print {
    background-color: $color-lightgray;
    background-image: url("/img/bg/category-no-print@1x.jpg");
    background-repeat: no-repeat;
    background-position: center right;
}

  &--accessories {
    background-color: $color-very-lightgray;
    background-image: url("/img/bg/category-accessories@1x.jpg");
    background-repeat: no-repeat;
    background-position: center right;
  }

  &--home {
    background-color: $color-lightbrown;
    background-image: url("/img/bg/category-home@1x.png");
    background-repeat: no-repeat;
    background-position: center right;
}

  &--travel {
    background-color: $color-lightgray;
    background-image: url("/img/bg/category-travel@1x.png");
    background-repeat: no-repeat;
    background-position: center right;
  }

  &--office {
    background-color: $color-very-lightgray;
    background-image: url("/img/bg/category-office@1x.png");
    background-repeat: no-repeat;
    background-position: center right;
  }
}
