.radio {
  margin-left: auto;
  width: 70%;

  @media (max-width: $viewport--tablet) {
    width: 100%;
  }
}

.radio__wrap {
  margin-bottom: 7px;
  padding-top: 8px;

  @media (max-width: $viewport--desktop) {
    padding-left: 0;
  }

  @media (max-width: $viewport--tablet) {
    margin-bottom: 37px;
    padding: 0;
  }
}

.radio__item {
  padding-left: 58px;

  @media (max-width: $viewport--tablet) {
    padding-left: 64px;
  }
}

.radio__item label {
  position: relative;

  font-size: 16px;
  font-weight: 700;
  line-height: 1.2;

  cursor: pointer;
  letter-spacing: 0.01em;
  text-transform: uppercase;

  color: $color-default-black;
  transition: opacity $default-transition-settings;

  &::before,
  &::after {
    content: "";
    position: absolute;

    border-radius: 50%;
  }

  &::before {
    top: 1px;
    left: -50px;

    width: 22px;
    height: 22px;

    border: 1px solid $color-default-black;
  }

  &::after {
    top: 7px;
    left: -44px;

    width: 10px;
    height: 10px;

    background-color: $color-default-black;
  }

  @media (max-width: $viewport--tablet) {
    display: block;

    font-size: 12px;
    line-height: 12px;

    letter-spacing: 0.02em;
    text-transform: none;
  }
}

.radio__item input:checked+ label::before {
  border: none;
  background-color: $color-lightgreen;
}

.radio__item input:hover+ label,
.radio__item input:focus+ label::before {
  opacity: 0.5;
}

.form-order__item .radio span {
  display: block;

  margin: 0 0 6px 60px;
  width: auto;

  font-size: 14px;
  line-height: 1.2;

  opacity: 0.5;

  @media (max-width: $viewport--desktop) {
    font-size: 10px;
    line-height: 12px;

    letter-spacing: 0.02em;

    color: rgba(0, 0, 0, 0.5);
  }

  @media (max-width: $viewport--tablet) {
    margin: 0 0 12px 65px;
  }
}

.radio__text-address {
  position: relative;

  margin-left: auto;
  padding: 0 45px 6px 28px;
  width: 87%;

  svg {
    position: absolute;
    top: 9px;
    left: 2px;

    width: 12px;
    height: 16px;
  }

  @media (max-width: $viewport--desktop) {
    padding-right: 81px;
    width: 90%;

    svg {
      top: 9px;
    }
  }

  @media (max-width: $viewport--desktop) {
    margin-left: 65px;
    padding: 0 65px 0 28px;
    width: 81%;

    svg {
      top: 4px;
    }
  }
}

.radio__wrap .radio__text-address p {
  margin: 0;

  font-size: 18px;
  font-weight: 400;
  line-height: 30px;

  opacity: 1;
  color: $color-default-black;
}

.radio__item--map {
  margin-bottom: 45px;
  padding: 0;

  @media (max-width: $viewport--tablet) {
    margin-bottom: 40px;
  }
}

.radio__img-map {
  @media (max-width: $viewport--tablet) {
    display: none;
  }
}

.radio__item a {
  display: block;

  margin-bottom: 15px;
  padding-left: 63px;

  font-size: 10px;
  line-height: 1.1;

  text-decoration: underline;
  transition: opacity $default-transition-settings;

  @media (max-width: $viewport--tablet) {
    margin: 0 auto 20px 65px;
    padding: 9px;
    width: 60%;

    font-size: 12px;
    font-weight: 600;
    line-height: 1.2;

    text-align: center;
    text-decoration: none;
    letter-spacing: 0.07em;
    text-transform: uppercase;

    opacity: 0.5;
    border: 1px solid $color-gray-light;
    border-radius: 4px;
  }

  &:hover,
  &:focus {
    text-decoration: none;

    opacity: 0.5;
    outline: 0;

    @media (max-width: $viewport--tablet) {
      opacity: 1;
    }
  }
}

.order__inner .radio__wrap p {
  @media (max-width: $viewport--tablet) {
    margin-bottom: 13px;
    padding-left: 0;

    font-size: 10px;
    line-height: 12px;
  }
}

.form-order__item--payment .radio__wrap {
  margin-bottom: 27px;
  padding: 0 0 0 63px;


  @media (max-width: $viewport--desktop) {
    padding-left: 50px;
  }

  @media (max-width: $viewport--tablet) {
    padding-left: 64px;
  }
}

.order .form-order__payment-radio span:first-of-type {
  margin-bottom: 32px;
}
