.field-num__inner {
  position: relative;

  display: flex;
  justify-content: space-between;
  align-items: center;

  width: 103px;

  border: 1px solid $color-gray;
  border-radius: 2px;

  @media (max-width: $viewport--tablet) {
    width: 65px;

    border: 0;
  }
}

.field-num__btn {
  position: relative;

  overflow: visible;

  margin: 0;
  padding: 5px;
  width: 33px;
  height: 33px;

  font-size: 0;

  cursor: pointer;
  transition: opacity $default-transition-settings;
  text-transform: none;

  color: transparent;
  border: 0;
  outline: 0;
  background-color: transparent;

  touch-action: manipulation;

  &::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;

    width: 13px;
    height: 2px;

    transform: translate(-50%, -50%);

    background-color: $color-default-black;
  }

  @media (max-width: $viewport--tablet) {
    width: 16px;
    height: 16px;

    border-radius: 50%;
    background-color: $color-default-black;

    &::before {
      width: 8px;
      height: 1px;

      background-color: $color-default-white;
    }
  }

  &--plus {
    order: 1;

    &::after {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;

      width: 13px;
      height: 2px;

      transform: translate(-50%, -50%) rotate(90deg);

      background-color: $color-default-black;
    }

    @media (max-width: $viewport--tablet) {
      &::after {
        width: 8px;
        height: 1px;

        background-color: $color-default-white;
      }
    }
  }

  &--minus {
    &::before {
      opacity: 0.5;
    }

    @media (max-width: $viewport--tablet) {
      background-color: $color-gray;

      &::before {
        opacity: 1;
      }
    }
  }

  &:hover,
  &:focus {
    opacity: 0.3;
  }

  &:active {
    transform: none;
  }
}

.field-num__inner input {
  margin: 0;
  padding: 2px;
  width: 33px;

  font-family: inherit;
  font-size: 20px;
  line-height: 1.25;

  text-align: center;

  color: $color-default-black;
  border: 0;
  outline: 0;

  @media (max-width: $viewport--tablet) {
    width: 20px;

    font-size: 14px;
  }
}
