@media (min-width: $viewport--desktop) {
  .header--main {
    position: relative;

    margin-bottom: -62px;

    background-image: url("/img/bg/header-line-desktop@1x.jpg");
    background-position: top 0 center;
    background-repeat: repeat;
    background-size: 1280px 2px;

    // Зеленый козырек на главной странице
    &::before {
      position: absolute;
      top: auto;
      bottom: calc(100% - 281px);
      left: calc(50% - 325px);
      z-index: 1;

      width: 100%;
      height: 500px;
      background-color: $color-lightgreen;
      transform: rotate(-11deg) skew(47deg);
      transform-origin: left bottom;
      content: "";
    }
  }
}

@media (max-width: $viewport--desktop - 1) {
  .header--main {
    margin-bottom: -57px;
    background-color: $color-about-gray;
  }
}
