.gift {
  padding: 17px 0 40px;
  width: 100%;
  @media (max-width: $viewport--tablet) {
    padding-top: 17px;
  }

  @media (max-width: $viewport--tablet) {
    order: 2;

    padding-bottom: 10px;
  }
}

.gift p {
  position: relative;

  margin: 0 0 30px;
  padding: 0 0 0 50px;

  font-size: 22px;
  font-weight: 600;
  line-height: 1.2;

  letter-spacing: 0.05em;

  svg {
    position: absolute;
    top: -7px;
    left: 0;

    width: 28px;
    height: 30px;
  }

  @media (max-width: $viewport--tablet) {
    margin-bottom: 17px;
    padding-left: 38px;

    font-size: 15px;

    letter-spacing: 0.03em;
    text-transform: uppercase;

    svg {
      top: -5px;

      width: 20px;
      height: 22px;
    }
  }


  @media (max-width: $viewport--tablet) {
    svg {
      top: 0;
      left: 16px;

      width: 15px;
      height: 17px;
    }
  }
}

.gift__list {
  display: flex;
  flex-wrap: wrap;

  margin: 0;
  padding: 0;

  list-style: none;

  @media (max-width: $viewport--desktop) {
    justify-content: space-between;

    padding: 0;
  }

  @media (max-width: $viewport--tablet) {
    justify-content: center;

    border-bottom: 1.5px dashed $color-default-black;
  }
}

.gift__item {
  margin: 0 27px 20px 0;

  opacity: 0.5;
  border: 5px solid transparent;
  border-radius: 2px;

  @media (max-width: $viewport--desktop) {
    width: 30%;
  }

  @media (max-width: $viewport--tablet) {
    margin: 0 10px 15px 0;
    width: 31%;

    border-width: 2px;
  }

  &:nth-child(3n) {
    margin-right: 0;
  }
}


.is-gift-active {
  opacity: 1;
  border-color: $color-lightgreen;
}
