.order {
  padding: 73px 0 100px;

  @media (max-width: $viewport--tablet) {
    padding: 22px 0 35px;
  }
}

.order h1 {
  margin: 0 0 17px;
  padding: 0;

  font-size: 28px;
  font-weight: 600;
  line-height: 1.3;

  letter-spacing: 0.03em;
  text-transform: uppercase;

  @media (max-width: $viewport--tablet) {
    position: relative;

    margin-bottom: 24px;
    padding-left: 40px;

    font-size: 16px;

    text-transform: none;
  }
}

.order__close {
  display: none;

  @media (max-width: $viewport--tablet) {
    position: absolute;
    top: -4px;
    left: 17px;

    display: block;

    width: 30px;
    height: 30px;
    border: 0;
    outline: 0;
    transition: opacity $default-transition-settings;

    &::before,
    &::after {
      width: 24px;
      height: 1px;

      background-color: $color-default-black;
    }

    &:hover {
      opacity: 0.5;
    }
  }
}

.order__inner {
  margin: 0 0 0 auto;
  padding: 0 15px;
  width: 88.9%;

  @media (max-width: $viewport--desktop) {
    margin-right: auto;
    padding: 0 40px;
    width: 100%;
  }

  @media (max-width: $viewport--tablet) {
    padding: 0;
  }
}

.order__head {
  @media (max-width: $viewport--tablet) {
    position: relative;

    padding: 0 23px 0 17px;
  }

  @media (max-width: $viewport--sm) {
    padding: 0 15px;
  }
}

.order__head p {
  display: inline-block;

  margin: 2px 15px 0 0;
  padding: 0;
  width: 179px;

  font-size: 10px;
  font-weight: 700;
  line-height: 1.5;

  vertical-align: top;

  opacity: 0.5;
  color: $color-default-black;

  @media (max-width: $viewport--tablet) {
    font-size: 12px;
    line-height: 14px;

    letter-spacing: 0.02em;
  }

  @media (max-width: $viewport--tablet) {
    margin: 0 7px 0 0;
    padding: 6px 0 0 16px;
    width: 47%;

    font-size: 12px;
    font-weight: 400;
    line-height: 13px;

    letter-spacing: 0.01em;
  }

  @media (max-width: $viewport--sm) {
    width: 45%;
    font-size: 10px;
  }
}

.order__wrap {
  display: flex;
  flex-wrap: wrap;

  @media (max-width: $viewport--desktop) {
    position: relative;
  }
}

.order__head-text {
  @media (max-width: $viewport--tablet) {
    padding: 0 23px 0 17px;
    width: 100%;
  }
}

.order__head-text p {
  display: none;

  @media (max-width: $viewport--tablet) {
    display: block;

    margin: 13px 0 0;
    padding: 0;
    width: auto;

    font-size: 14px;
    font-weight: 600;
    line-height: 12px;

    letter-spacing: 0.02em;

    opacity: 1;
    color: $color-default-black;
  }
}

.order__wrap p sup {
  @media (max-width: $viewport--tablet) {
    display: inline-block;

    margin-top: 6px;

    vertical-align: bottom;

    color: $color-star;
  }
}

.order__link-enter {
  display: inline-block;

  padding: 10px 5px;
  width: 143px;

  font-size: 12px;
  font-weight: 600;
  line-height: 1.3;

  text-align: center;
  vertical-align: top;
  letter-spacing: 0.02em;
  text-transform: uppercase;

  background-color: $color-lightgreen;
  transition: background-color $default-transition-settings;

  @media (max-width: $viewport--tablet) {
    width: 49.5%;
  }

  &:hover,
  &:focus {
    padding: 9px 5px;
    opacity: 1;
    background-color: transparent;
    border: 1px solid $color-default-black;
  }
}

.order__form {
  margin-top: 25px;

  @media (max-width: $viewport--tablet) {
    margin-top: 20px;
  }
}

.order__result p {
  margin: 0;

  font-size: 18px;
  font-weight: 400;
  line-height: 19px;

  opacity: 1;

  @media (max-width: $viewport--tablet) {
    padding: 0;

    font-size: 14px;
    font-weight: 600;
    line-height: 16px;

    letter-spacing: 0.02em;
  }
}

.order__result {
  margin: -20px -15px 0 auto;

  @media (max-width: $viewport--desktop) {
    margin: 0;
  }
}
