.banner {
  display: block;
  overflow: hidden;

  flex-shrink: 0;

  font-family: $font-family--open-sans;
  font-style: normal;
  font-weight: 700;
  font-size: 10px;
  line-height: 110%;

  color: $color-default-black;

  perspective: 500px;
  transform-style: preserve-3d;
  transform-origin: left top;

  &--dark {
    color: $color-default-white;
    background-color: $color-default-black;
  }

  &--light {
    color: $color-default-black;
    background-color: $color-default-white;
  }

  &--collection {
    position: relative;

    display: flex;
    justify-content: flex-start;
    align-items: flex-end;

    padding: 8px;

    width: 98px;
    min-height: 128px;

    border-radius: 2px;

    .banner__text {
      z-index: 2;
    }

    .banner__img {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;

      width: 100%;
      height: auto;
    }
  }

  &--wide {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-between;

    padding: 20px 23px;

    width: 100%;
    min-height: 170px;

    color: $color-default-white;

    background-color: $color-default-black;
    background-image: url("/img/svg/deco-black.svg"), url("/img/bg/banner-suit@1x.png");
    background-position: top 0 left 195px, top 0 left 0;
    background-repeat: no-repeat, no-repeat;
    background-size: 182px 170px, 375px 170px;

    .banner__strong {
      display: block;

      font-size: 75px;
      line-height: 1;
      font-weight: 800;
      color: $color-orange;

      text-transform: uppercase;
      transform: rotateY(45deg);
    }

    .banner__text {
      display: block;
      max-width: 145px;

      font-size: 16px;
      line-height: 18px;
      letter-spacing: 0.03em;
    }
  }

  &--category {
    position: relative;

    display: flex;
    justify-content: flex-end;
    align-items: flex-end;

    padding: 10px;
    min-height: 117px;

    color: $color-default-black;

    background-color: $color-lightblue;
    background-image: url("/img/bg/category-sale@1x.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 375px auto;

    .banner__strong {
      position: absolute;
      left: 30px;
      top: 0;

      display: block;

      font-size: 110px;
      line-height: 1;
      font-weight: 800;

      text-transform: uppercase;
      transform: rotateY(45deg);
      transform-origin: left top;
    }

    .banner__text {
      margin-top: auto;
      max-width: 115px;

      font-size: 12px;
      line-height: 16px;
      letter-spacing: 0.01em;

      text-transform: uppercase;
    }
  }
}

@media (min-width: $viewport--desktop) {
  .banner {
    perspective: 500px;
    transform-style: preserve-3d;
    transform-origin: left top;

    &--collection {
      width: 307px;
      height: 340px;
    }

    &--wide {
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      justify-content: space-between;

      flex-shrink: 0;
      padding: 27px;
      min-height: 450px;

      background-color: $color-default-black;
      background-image: url("/img/bg/banner-suit-desktop@1x.png");
      background-position: top 0 left 0;
      background-repeat: no-repeat;
      background-size: 971px 450px;

      // background-color: $color-default-black;
      // background-image: url("/img/svg/deco-black.svg"), url("/img/bg/banner-suit@1x.png");
      // background-position: top 0 left 195px, top 0 left 0;
      // background-repeat: no-repeat, no-repeat;
      // background-size: 182px 170px, 375px 170px;

      .banner__strong {
        display: block;

        font-size: 180px;
        line-height: 1;
        font-weight: 800;
        color: $color-orange;

        text-transform: uppercase;
        transform: rotateZ(0) rotateX(0) rotateY(45deg);
        transform-origin: left top;
      }

      .banner__text {
        max-width: 280px;
        font-size: 30px;
        line-height: 115%;
      }
    }
  }
}
