/*
 Это карточка товара, которая встречается по всему сайту
 То, что на странице выбранного товара, это, скорее всего, product-details
 */

.product {
  position: relative;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;

  padding-top: 23px;
  padding-bottom: 7px;

  width: 158px;
  height: 100%;

  font-family: $font-family--open-sans;
  font-size: 12px;
  line-height: 16px;

  color: $color-default-black;
  background-color: $color-default-white;
}

.product__favorites-btn {
  position: absolute;
  top: 12px;
  right: 12px;
}

.product__favorites-icon {
  display: block;
  stroke: $color-default-black;
  fill: transparent;
}

.product__link {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;

  margin-bottom: 10px;
}

.product__img {
  margin-left: 6px;
  margin-bottom: 3px;
  width: 138px;
  height: 153px;
}

.product__price,
.product__title {
  margin: 0;
  margin-left: 15px
}

.product__title {
  max-width: 158px - 15px;  // for IE11
  font-weight: 400;
}

.product__current-price {
  font-weight: 600;
}

.product__author-link {
  margin-top: auto; // for IE11
  margin-left: 15px;
  max-width: 100%; // for IE11
  letter-spacing: 0.01em;
  text-transform: uppercase;
  opacity: 0.5;
}

@media (min-width: $viewport--desktop) {
  .product {
    width: 270px;
  }

  .product__img {
    margin-left: 12px;
    margin-bottom: 3px;
    width: 240px;
    height: 278px;
  }

  .product__price,
  .product__title {
    margin-left: 27px;
  }

  .product__current-price {
    font-size: 28px;
    line-height: 128%;
    letter-spacing: 0.03em;
  }

  .product__title {
    max-width: 240px - 27px;  // for IE11
    font-size: 18px;
    line-height: 22px;
  }

  .product__author-link {
    margin-left: 27px;
    opacity: 0.7;
  }
}
