.social {
  display: flex;
  flex-wrap: wrap;
}

.social__item {
  margin: 0 14px 14px 0;

  @media (max-width: $viewport--tablet) {
    margin-right: 37px;
  }

  &:last-child {
    margin-right: 0;
  }
}

.footer .social__link {
  display: block;

  width: 22px;
  height: 22px;

  cursor: pointer;
  transition: transform $default-transition-settings;

  outline: none;

  @media (max-width: $viewport--tablet) {
    width: 30px;
    height: 30px;

    &--youtube {
      margin: 2px 0 0 5px;
      width: 40px;
      height: 27px;
    }
  }

  svg {
    width: 100%;
    height: 100%;

    fill: $color-default-white;
  }

  &:hover,
  &:focus {
    transform: scale(1.2);
  }
}
