.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  clip: rect(0 0 0 0);
  overflow: hidden;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

@media (min-width: $viewport--desktop) {
  .wrapper {
    margin: 0 auto;
    padding: 0 155px;
    max-width: 1280px;
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;

  z-index: 20;
  display: none;

  width: 100%;
  height: 100%;

  background-color: $color-gray;
}

.no-scroll {
  overflow: hidden;
  height: 100vh;
}
