.popup-favourites {
  display: none;
  @media (max-width: $viewport--desktop) {

    width: 100%;
    top: 103px;
    padding: 20px 21px 20px;

    background-color: $color-default-white;
  }
}

.popup-favourites__btn {
  position: relative;

  margin-bottom: 13px;
  padding: 12px 77px;
  width: 100%;

  transition: opacity $default-transition-settings;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  border: 1px solid $color-default-black;

  svg {
    position: absolute;
    top: 50%;
    left: 62px;

    transform: translateY(-50%);

    fill: none;
    stroke: $color-default-black;
  }

  &--remove {
    padding: 12px 130px;

    border: 0;
    background-color: $color-gray-light;

    svg {
      left: 113px;
    }
  }

  &:hover {
    opacity: 0.5;
  }
}

.is-open-popup {
  display: block;
}
