/* Desktop */

@media (min-width: $viewport--desktop) {

  /* header-top ordinal */

  .header-top {
    position: relative;
    z-index: 1;

    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: flex-start;

    padding: 26px 25px 28px 62px;

    // Эффект скошенного заголовка
    &::after {
      position: absolute;
      bottom: 0;
      left: 0;
      z-index: -1;
      width: calc(100% + 100px);
      height: 100%;
      transform: rotate(-2deg);
      transform-origin: left bottom;
      background-color: $color-lightgreen;

      content: "";
    }
  }

  .header-top__logo-img {
    width: 121px;
    height: 56px;
  }

  .header-top__gift-link {
    display: block;
    margin-left: auto;
    margin-top: 6px;
    padding: 2px 7px 3px;

    color: $color-default-black;

    font-weight: 600;
    font-size: 9px;
    line-height: 128%;

    letter-spacing: 0.08em;
    text-transform: uppercase;

    background: rgba(255, 255, 255, 0.8);
    border-radius: 4px;

    &.is-not-visible {
      display: none;
    }
  }

  .header-top__form {
    display: none;
  }

  .header-top__form.is-visible {
    display: flex;
    margin-left: auto;
  }

  .header-top__user-nav {
    margin-left: 15px;
    padding-top: 4px;
    width: auto;
    max-width: 160px;
  }

  .header-top__btn {
    order: -1;
    margin-top: 12px;
    margin-right: 65px;
  }

  /* header-top is-main */

  .header-top.is-main {
    position: relative;
    z-index: 2;

    padding: 40px 39px 34px 155px;

    &::after {
      content: none;
    }

    .header-top__main-nav {
      display: flex;
      // 160px = блок пользователя, 15px - его левая граница
      width: calc(100% - 160px - 15px);
    }

    .header-top__gift-link {
      display: none;
    }

    .header-top__user-nav {
      margin-left: auto;
      padding-top: 14px;
    }

    .header-top__btn {
      display: none;
    }
  }

  /* header-top is-sticky */

  .header-top.is-sticky {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    width: 100%;
  }

  /* header-top is-open */

  .header-top.is-open:not(.is-main) {
    flex-direction: column;
    align-items: flex-start;
    align-content: space-between;
    flex-wrap: wrap;

    padding: 28px 52px 28px 68px;
    height: 100vh;

    background-color: $color-default-black;

    &::after {
      content: none;
    }

    .header-top__main-nav {
      width: 50%;
      height: 100%;
    }

    .header-top__logo-img {
      display: none;
    }

    .header-top__btn {
      display: none;
    }

    .header-top__gift-link {
      align-self: flex-end;
      margin-top: 5px;
      margin-left: 0;
      margin-right: 88px;
      margin-bottom: -20px;
      z-index: 1;
    }

    .header-top__user-nav {
      margin-left: 0;
      padding-top: 0;
      height: 153px;
      width: 50%;
      max-width: 50%;
    }

    .header-top__form {
      order: 1;
      display: flex;
      margin-bottom: 121px;
      margin-left: 184px;
      padding-bottom: 7px;

      width: 358px;

      border-bottom: 1px solid $color-default-white;

      & input {
        color: $color-default-white;
      }

    }

    .header-top__search-form-icon {
      display: block;
      fill: $color-default-white;
    }

    .header-top__promo {
      order: 2;
      display: block;
      margin-left: 171px;
      width: 378px;
      min-height: 359px;
    }

    .header-top__promo-img {
      display: block;
      margin: 0 auto;

      &:first-of-type {
        margin-bottom: 64px;
      }
    }
  }
}

/* Mobile */

@media (max-width: $viewport--desktop - 1) {

  /* header-top ordinal */

  .header-top {
    position: relative;
    z-index: 2;

    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;

    padding: 13px 21px 10px 27px;
  }

  .header-top__btn-icon {
    display: block;
  }

  .header-top__gift-link {
    display: none;
  }

  .header-top__form {
    display: none;
  }

  /* header-top is-main */

  .header-top.is-main {
    padding-top: 23px;
    padding-bottom: 12px;

    .header-top__main-nav {
      display: none;
    }

    @media (max-width: $viewport--desktop - 1) {
      .header-top__cart-icon {
        fill: $color-default-white;
      }

      .header-top__favorites-icon,
      .header-top__user-icon {
        stroke: $color-default-white;
        fill: transparent;
      }
    }
  }

  /* header-top is-open */

  .header-top.is-open {
    .header-top__main-nav {
      display: flex;
    }
  }
}
