.description-list {
  display: flex;
  flex-wrap: wrap;

  margin: 0;
  padding-top: 7px;
  width: 160px;

  @media (max-width: $viewport--tablet) {
    padding-top: 5px;
    width: 110px;
  }
}

.description-list dt {
  display: block;

  margin: 0 0 10px;

  font-size: 14px;
  line-height: 1.5;

  opacity: 0.7;
  color: $color-default-black;

  @media (max-width: $viewport--tablet) {
    margin: 0 0 4px;

    font-size: 10px;
    line-height: 1.25;

    letter-spacing: 0.02em;

    opacity: 0.7;
  }
}

.description-list dd {
  margin: 0 0 10px 35px;

  font-size: 18px;
  font-weight: 700;
  line-height: 1.2;

  letter-spacing: 0.01em;

  opacity: 0.85;

  @media (max-width: $viewport--tablet) {
    margin: 0 0 4px 30px;
    font-size: 12px;

    opacity: 0.85;
  }
}

.description-list dd:last-of-type {
  margin-left: 20px;
}
