.slider {
  overflow: hidden;
  padding: 5px 0 28px;

  @media (max-width: $viewport--tablet) {
    padding-top: 25px;
  }
}

.slider__inner {
  position: relative;

  padding: 0 105px 50px;

  @media (max-width: $viewport--desktop) {
    padding: 0 70px;
  }

  @media (max-width: $viewport--tablet) {
    padding: 0 0 15px;
    width: 115%;

    transform: translateX(-13%);
  }
}

.slider h2 {
  margin: 0 0 63px;
  padding-left: 50px;

  font-size: 28px;
  font-weight: 600;

  letter-spacing: 0.03em;
  text-transform: uppercase;

  @media (max-width: $viewport--desktop) {
    padding-left: 0;

    text-align: center;
  }

  @media (max-width: $viewport--tablet) {
    display: none;
  }
}

.slider p {
  display: none;

  @media (max-width: $viewport--desktop) {
    display: block;

    margin: 0 0 10px;
    padding: 0 0 0 30px;

    font-size: 15px;
    font-weight: 600;
    line-height: 1.3;

    letter-spacing: 0.03em;
    text-transform: uppercase;

    @media (max-width: $viewport--tablet) {
      transform: translate(13%);
    }
  }
}

.slider .swiper-container {
  position: relative;

  height: auto;

  @media (max-width: $viewport--desktop) {
    justify-content: space-between;
  }

  @media (max-width: $viewport--tablet) {
    overflow: hidden;
  }
}

.slider .swipper-slide {
  padding: 5px 0 28px;


  @media (max-width: $viewport--tablet) {
    width: 151px;
  }
}

.slider .slider__item {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 25px 0 20px;
  width: 100%;
  height: auto;

  @media (max-width: $viewport--tablet) {
    padding-top: 10px;
    width: 150px;
  }
}

.slider__btn-favourites {
  position: absolute;
  top: 15px;
  right: 2px;
  display: block;
  padding: 0;
  z-index: 100;

  width: 23px;
  height: 22px;

  cursor: pointer;
  transition: transform $default-transition-settings;

  outline: none;

  @media (max-width: $viewport--tablet) {
    top: 8px;
    right: -4px;
    width: 12px;
    height: 10px;
  }

  svg {
    width: 100%;
    height: 100%;

    stroke: $color-default-black;
    fill: transparent;
  }

  &:hover,
  &:focus {
    opacity: 1;
    transform: scale(1.1);
  }
}

.slider__link {
  display: flex;
  flex-direction: column;

  flex-grow: 1;

  transition: opacity $default-transition-settings;

  outline: 0;

  &:hover,
  &:focus {
    opacity: 0.7;
  }
}

.slider__img-wrap {
  margin-bottom: 10px;

  @media (max-width: $viewport--tablet) {
    margin-bottom: 7px;
  }
}

.slider__img-wrap img {
  @media (max-width: $viewport--desktop) {
    width: 324px;
    height: 340px;
  }

  @media (max-width: $viewport--tablet) {
    width: 146px;
    height: 179px;
  }
}

.slider .slider__item p {
  display: block;

  margin: 0 0 27px;

  font-size: 18px;
  line-height: 22px;

  transform: translate(12%);
  text-align: left;

  color: $color-default-black;

  @media (max-width: $viewport--tablet) {
    margin-bottom: 29px;
    padding: 0 0 0 19px;

    font-size: 10px;
    line-height: 11px;

    transform: none;
    text-transform: none;
  }
}

.slider__item span {
  display: block;

  margin-bottom: 16px;

  font-size: 12px;
  line-height: 16px;

  transform: translate(12%);
  letter-spacing: 0.01em;
  text-transform: uppercase;

  opacity: 0.7;

  @media (max-width: $viewport--tablet) {
    display: none;
  }
}

.slider__btn {
  display: block;

  margin: 0 auto;
  padding: 10px;
  width: 80%;

  font-size: 12px;
  font-weight: 600;
  line-height: 1.3;

  transform: translateX(7px);
  text-align: center;
  letter-spacing: 0.02em;
  text-transform: uppercase;

  color: $color-default-black;
  background-color: $color-lightgreen;
  transition: background-color $default-transition-settings;

  @media (max-width: $viewport--tablet) {
    margin: 0 auto 0 19px;
    padding: 5px;
    width: 125px;

    font-size: 8px;
    line-height: 1.3;

    transform: none;
    text-align: center;
    letter-spacing: 0.02em;
    text-transform: uppercase;

    border: 0.6px solid rgba(0, 0, 0, 0.5);
    background-color: transparent;
  }

  &:hover {
    padding: 9px;

    opacity: 1;
    border: 1px solid $color-default-black;
    background-color: transparent;

    @media (max-width: $viewport--tablet) {
      background-color: $color-lightgreen;
    }
  }
}

.slider__arrow {
  position: absolute;
  top: 243px;
  z-index: 100;

  margin: 0;
  padding: 0;
  width: 20px;
  height: 40px;

  font-size: 0;

  color: transparent;
  outline: none;
  background-color: transparent;
  transition: opacity $default-transition-settings;

  &::before,
  &::after {
    content: "";
    position: absolute;
    left: 50%;

    width: 2px;
    height: 15px;

    background-color: $color-lightgreen;
  }

  @media (max-width: $viewport--tablet) {
    display: none;
  }

  &--prev {
    left: 63px;

    &::before {
      top: 7px;

      transform: rotate(45deg) translateX(-50%);
    }

    &::after {
      bottom: 9px;

      transform: rotate(-45deg) translateX(-50%);
    }

    @media (max-width: $viewport--desktop) {
      left: 40px;
    }
  }

  &--next {
    right: 63px;

    &::before {
      top: 2px;

      transform: rotate(-45deg) translateX(-50%);
    }

    &::after {
      bottom: 12px;

      transform: rotate(45deg) translateX(-50%);
    }

    @media (max-width: $viewport--desktop) {
      right: 40px;
    }
  }

  &:hover,
  &:focus {
    opacity: 0.4;
  }
}
