.registration {
  width: 100%;

  @media (max-width: $viewport--tablet) {
    padding-right: 4px;
  }
}

.registration__item {
  display: flex;
  justify-content: space-between;
  align-items: center;

  margin-bottom: 26px;
  width: 100%;

  @media (max-width: $viewport--tablet) {
    margin-bottom: 14px;
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.registration__item:nth-of-type(2) {
  margin-bottom: 10px;

  @media (max-width: $viewport--tablet) {
    margin-bottom: 12px;
  }
}

.registration__item:nth-of-type(3) {
  margin: 0 0 15px auto;
  width: 70%;

  line-height: 20px;

  @media (max-width: $viewport--tablet) {
    margin: 0 0 7px auto;
    width: 100%;
  }
}

.registration__item label {
  @media (max-width: $viewport--tablet) {
    display: none;
  }
}

.registration__item input {
  display: inline-block;

  margin: 0;
  padding: 12px 16px 12px;
  width: 70.4%;

  cursor: pointer;
  vertical-align: top;
  letter-spacing: 0.02em;

  color: $color-default-black;
  border: 1px solid $color-default-black;
  border-radius: 2px;
  outline: 0;
  background-color: transparent;

  appearance: none;

  @media (max-width: $viewport--desktop) {
    width: 71%;
  }

  @media (max-width: $viewport--tablet) {
    width: 100%;

    border-color: $color-gray-light;
  }
}

.registration__item input::placeholder {
  font-size: 14px;
  line-height: 1;
  overflow: visible;

  @media (max-width: $viewport--tablet) {
    font-size: 11px;
    font-weight: 600;
    line-height: 1.3;

    letter-spacing: 0.02em;

    opacity: 0.5;
    color: $color-default-black;
  }
}

.registration label sup {
  display: inline-block;

  margin-top: 9px;

  font-size: 18px;

  vertical-align: bottom;

  color: $color-star;
}

.registration .checkbox span {
  font-weight: 700;
}
