.breadcrumbs {
  display: flex;
  flex-wrap: wrap;
}

.breadcrumbs__item {
  margin-right: 5px;
}

.breadcrumbs__item a {
  position: relative;

  display: block;

  padding-right: 17px;

  font-size: 10px;
  font-weight: 700;
  line-height: 1.5;

  opacity: 0.5;

  svg {
    position: absolute;
    top: 50%;
    right: 0;

    width: 11px;
    height: 9px;
    transform: translateY(-50%);
  }
}

.breadcrumbs__item--curent a {
  opacity: 1;
}
