.categories {
  padding-top: 42px;
  padding-bottom: 32px;
  color: $color-default-black;
  background-color: $color-default-white;
}

.categories__title {
  margin-left: 22px;
  margin-bottom: 42px;
}

.categories__item {
  & + & {
    margin-top: 12px;
  }
}

@media (min-width: $viewport--desktop) {
  .categories__wrapper {
    padding: 0 47px;
  }

  .categories__title {
    margin-left: 0;
  }

  .categories__item {
    display: none;
  }

  .categories__slider {
    display: block;
    height: 293px;
  }

  .categories__slide {
    display: block;
    width: 390px;
    height: 293px;

    background-color: khaki;
  }
}
