.user-nav__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;

  padding: 0;
  margin: 0;
  list-style: none;
}

.user-nav__link {
  display: block;
}

.user-nav__link-text {
  display: none;
}

.user-nav__cart-icon {
  display: block;
  fill: $color-default-black;
}

.user-nav__favorites-icon,
.user-nav__user-icon {
  display: block;
  stroke: $color-default-black;
  fill: transparent;
}

@media (min-width: $viewport--desktop) {
  .user-nav__list {
    justify-content: flex-end;
  }

  .user-nav__item {
    & + & {
      margin-left: 26px;
    }

    &--search {
      display: block;
    }
  }

  .user-nav.is-open {
    position: relative;

    .user-nav__item--search {
      display: none;
    }

    .user-nav__item--cabinet {
      position: absolute;
      top: calc(100% + 63px);
      left: 184px;

      margin-left: 0;

      .user-nav__link {
        display: flex;
      }

      .user-nav__user-icon {
        flex: 0 0 18px;
        margin-right: 18px;
        height: auto;
      }

      .user-nav__link-text {
        display: block;

        font-size: 15px;
        line-height: 128%;
        letter-spacing: 0.02em;
        color: $color-default-white;
      }
    }

    .user-nav__cart-icon {
      fill: $color-default-white;
    }

    .user-nav__favorites-icon,
    .user-nav__user-icon {
      stroke: $color-default-white;
      fill: transparent;
    }
  }
}

@media (max-width: $viewport--desktop - 1) {
  .user-nav {
    width: 40%;
  }
}
