.price {
  width: 98px;

  text-align: center;

  @media (max-width: $viewport--tablet) {
    width: 50px;
  }
}

.price .price__discount {
  position: relative;

  margin: 0 0 6px;

  font-size: 16px;
  font-weight: 600;
  line-height: 24px;

  transform: translate(7px);

  opacity: 0.5;

  &::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;

    width: 50px;
    height: 0.2px;

    transform: translate(-50%, -50%);

    background-color: $color-dark;
  }

  @media (max-width: $viewport--tablet) {
    padding-top: 0;

    font-size: 11px;
    line-height: 14px;

    transform: none;

    &::after {
      width: 40px;
      height: 0.2px;
    }
  }
}

.price span {
  display: none;

  @media (max-width: $viewport--tablet) {
    display: block;

    font-size: 11px;
    line-height: 18px;

    letter-spacing: 0.05em;

    opacity: 0.5;
  }
}

.result .price {
  padding: 0;
  width: 50px;

  @media (max-width: $viewport--tablet) {
    padding-right: 20px;
    width: 130px;

    text-align: right;
  }
}

.cart .result .price {
  padding: 0;
  width: 100px;

  @media (max-width: $viewport--tablet) {
    padding-right: 22px;
    width: 138px;
  }
}

.result .price__discount {
  font-size: 18px;
  line-height: 22px;

  letter-spacing: 0.02em;

  opacity: 0.5;
  color: $color-default-black;


  &::after {
    width: 65px;
  }

  @media (max-width: $viewport--tablet) {
    font-size: 15px;
    line-height: 20px;

    &::after {
      width: 57px;
    }

    svg {
      width: 10px;
      height: 11px;
    }
  }
}

.cart .result .price__discount {
  margin-left: 12px;
}

.slider .price {
  display: flex;

  margin-bottom: 3px;
  padding: 0;
  width: auto;

  font-size: 28px;
  line-height: 1.3;

  transform: translate(12%);
  letter-spacing: 0.02em;
  text-transform: uppercase;

  @media (max-width: $viewport--tablet) {
    font-size: 12px;
    line-height: 16px;

    transform: none;
  }
}

.slider .price svg {
  @media (max-width: $viewport--tablet) {
    width: 9px;
    height: 10px;
  }
}

.slider .price__discount {
  order: 1;

  margin: 10px 0 0 10px;

  @media (max-width: $viewport--tablet) {
    display: none;
  }
}

.slider .price b {
  @media (max-width: $viewport--tablet) {
    padding-left: 19px;
    line-height: 12px;
  }
}

.order .price__discount {
  display: block;

  margin: 3px 0 0;

  font-size: 10px;
  font-weight: 700;
  line-height: 11px;

  text-align: right;

  &::after {
    top: 5px;
    left: 36px;

    width: 30px;
  }
}

.order__result .price {
  padding-right: 0;
  width: 46px;
}

.cart__item .price .price__discount {
  @media (max-width: $viewport--tablet) {
    margin-bottom: 0;

    svg {
      width: 7px;
      height: 9px;
    }
  }
}

.cart__description .price b:last-of-type {
  @media (max-width: $viewport--tablet) {
    margin-top: 0;
  }
}
