.promo-code {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  align-content: space-between;

  padding: 18px 15px 34px 33px;

  font-family: $font-family--open-sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 100%;

  letter-spacing: normal;

  color: $color-default-black;
}

.promo-code__text {
  margin: 0%;
  padding-right: 10px
}

.promo-code__code {
  position: relative;

  margin: 10px 0;
  padding-right: 10px;

  font-size: 24px;

  letter-spacing: 0.3em;
  text-transform: uppercase;

  &::after {
    position: absolute;
    bottom: -10px;
    left: 0;
    right: 0;
    width: calc(100% - 10px);
    height: 1px;
    background-image: repeating-linear-gradient(to right, $color-default-black 0, $color-default-black 14.5px, transparent 14.5px, transparent calc(14.5px + 10px));
    font: inherit;
    content: "";
  }
}

.promo-code__btn {
  padding-left: 0;
  padding-right: 0;

  font-size: 10px;
  font-weight: 400;

  line-height: 81%;

  text-decoration: underline;
  text-transform: capitalize;
}

@media (min-width: $viewport--desktop) {
  .promo-code {
    display: none;
  }
}
