.prints {
  padding: 45px 0;
}

@media (max-width: $viewport--desktop - 1) {
  .prints__wrapper {
    padding: 0 18px;
  }
}

.prints__title {
  margin-bottom: 19px;
}

.prints__list {
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  align-content: flex-start;
  flex-wrap: wrap;

  margin: 0;
  margin-bottom: 5px;
  padding: 0;

  list-style: none;
}

.prints__item {
  flex-shrink: 0;
}

@media (min-width: $viewport--desktop) {
  .prints {
    padding-top: 91px;
    padding-bottom: 23px;

    &--no-print {
      display: block;

      &--prints__wrapper {
        padding: 0 70px;
      }
    }
  }

  .prints__title {
    margin-bottom: 43px;
  }
}
