.promo {
  padding: 0;
}

.promo__slider {
  margin-bottom: 27px;
  width: 100%;
  min-height: 240px;
  background-color: $color-lightgray;
}

.promo__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  overflow: hidden;

  @media (max-width: $viewport--desktop - 1) {
    padding: 27px 0;
  }
}

@media (min-width: $viewport--desktop) {
  .promo__wrapper {
    justify-content: space-between;
  }

  .promo__item {
    margin-bottom: 23px;
  }

  .promo__slider {
    margin-bottom: 0;
    width: 654px;
    min-height: 340px;
  }
}
