.form-order {
  width: 60%;

  line-height: 1.5;

  @media (max-width: $viewport--desktop) {
    width: 100%;
  }

  @media (max-width: $viewport--tablet) {
    padding: 0 21px 0 17px;
  }

  @media (max-width: $viewport--sm) {
    padding: 0 15px;
  }
}

.form-order p {
  margin: 0;
  padding: 0;

  font-size: 18px;
  line-height: 30px;

  @media (max-width: $viewport--desktop) {
    margin: 0 0 22px 4px;

    font-size: 14px;
    font-weight: 600;
    line-height: 22px;

    letter-spacing: 0.02em;
  }
}

.form-order label {
  display: inline-block;

  font-size: 18px;
  line-height: 30px;

  cursor: pointer;
  vertical-align: top;
  transition: opacity $default-transition-settings;

  &:hover,
  &:focus {
    text-decoration: none;

    opacity: 0.7;
  }
}

.form-order input {
  border-radius: 0;

  appearance: none;
  transition: opacity $default-transition-settings;

  &:hover,
  &:focus {
    opacity: 0.6;
  }
}

.form-order__item {
  display: flex;

  margin-bottom: 38px;

  @media (max-width: $viewport--tablet) {
    display: block;

    margin-bottom: 12px;
  }

  &:nth-of-type(2) {
    margin-bottom: 70px;

    @media (max-width: $viewport--tablet) {
      margin-bottom: 36px;
    }
  }

  &:nth-of-type(3) {
    margin-bottom: 25px;

    @media (max-width: $viewport--tablet) {
      margin-bottom: 36px;
    }
  }

  &:nth-of-type(4) {
    margin-bottom: 12px;

    @media (max-width: $viewport--tablet) {
      margin-bottom: 19px;
    }
  }

  &:nth-of-type(5) {
    margin: 0 0 23px auto;
    width: 70%;

    @media (max-width: $viewport--tablet) {
      margin: 0 0 16px auto;
      width: 100%;
    }
  }
}

.form-order__item:nth-of-type(4) p {
  @media (max-width: $viewport--tablet) {
    margin-bottom: 12px;
  }
}

.form-order__result {
  margin: -20px -15px 0 auto;
  height: 420px;
}

.form-order__comment-textarea {
  margin-left: auto;
  width: 70%;

  @media (max-width: $viewport--tablet) {
    padding-left: 4px;
    width: 100%;
  }
}

.form-order__comment-textarea textarea {
  padding: 5px;
  width: 100%;
  height: 116px;
  border-radius: 2px;
  border: 1px solid $color-default-black;
  outline: none;
}

.form-order__btn {
  margin: 0 12px 0 auto;
  padding: 10px 5px;
  width: 70%;

  font-size: 12px;
  font-weight: 600;
  line-height: 1.3;

  transform: translateX(2%);
  text-align: center;
  letter-spacing: 0.02em;
  text-transform: uppercase;

  border: 0;
  outline: 0;
  background-color: $color-lightgreen;
  transition: background-color $default-transition-settings;

  @media (max-width: $viewport--desktop) {
    margin-top: 510px;
    width: 100%;
  }

  @media (max-width: $viewport--tablet) {
    position: relative;
    z-index: 100;

    margin-top: 0;

    color: $color-default-white;
    background-color: $color-default-black;
  }

  &:hover,
  &:focus {
    padding: 9px 5px;
    opacity: 1;
    background-color: transparent;
    border: 1px solid $color-default-black;
    transform: translateX(2%);
  }

  &:active {
    transform: translateX(2%);
  }
}

.form-order__text {
  @media (max-width: $viewport--tablet) {
    padding: 0 0 350px 10px;
  }
}

.form-order__text p {
  display: none;

  @media (max-width: $viewport--tablet) {
    display: block;
    margin-left: 0;

    font-size: 14px;
    font-weight: 600;
    line-height: 14px;
  }
}
