@media (min-width: $viewport--desktop) {
  .search-form {
    position: relative;

    display: flex;
    justify-content: space-between;
    align-items: center;

    padding-top: 5px;
    width: 460px;

    border-bottom: 1px solid $color-default-black;
  }

  .search-form__label {
    flex: 1 0;
    padding-left: 14px;

    input {
      padding: 0;

      font-family: inherit;
      font-size: 14px;
      line-height: 128%;
      letter-spacing: 0.08em;

      border: 0;
      background-color: transparent;
      width: 100%;
    }
  }

  .search-form__icon {
    display: none;
  }

  .search-form__close-search-btn {
    flex: 0 1 13px;
    margin-left: 10px;
  }

  .search-form__close-search-icon {
    display: block;
  }

  .search-form__results {
    display: none;

    &.is-visible {
      position: absolute;
      top: calc(100% + 3px);
      left: 0;
      right: 0;
      z-index: 20;

      display: block;
      margin: 0;
      padding: 0 15px;

      box-shadow: inset 0 0 0 1px $color-lightgreen;
      background-color: $color-default-white;

      list-style: none;
    }
  }

  .search-form__result {
    padding: 9px 0 11px;

    & + & {
      border-top: 1px solid rgba(0, 0, 0, 0.15);
    }

    &:hover {
      background-color: rgba(0, 0, 0, 0.05);
    }

    &:active {
      opacity: 0.5;
    }
  }

  .search-form__result-link {
    display: flex;
    align-items: center;

    font-size: 14px;
    letter-spacing: normal;
  }

  .search-form__result-count {
    margin-left: 10px;
    color: rgba(0, 0, 0, 0.5);
    font-size: 10px;
  }

  .search-form__result-icon {
    margin-left: auto;
  }
}
