.gallery__inner {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.gallery__img-wrap {
  position: relative;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  margin-right: 15px;
  width: 52.3%;
}

.gallery__img-previews {
  width: 13.7%;
}

.gallery__img-preview {
  margin: 0 auto 10px;
  padding: 0;
  height: 79px;

  outline: 1px solid red;
}

.gallery__img-full {
  width: 82.7%;
  height: 683px;
}

.gallery__img-full img,
.gallery__img-preview img {
  width: 100%;
  height: 100%;

  font-family: "object-fit: cover";/*stylelint-disable*/

  object-fit: cover;
  object-position: center;
}

.gallery__info {
  padding-top: 15px;
  width: 511px;
}

.gallery__breadcrumbs {
  margin-bottom: 30px;
}

.gallery .gallery__inner p {
  margin: 0 0 10px;
  padding: 0;

  font-size: 14px;
  line-height: 1.3;

  letter-spacing: 0.02em;
  text-transform: uppercase;

  opacity: 0.7;
}

.gallery__inner h3 {
  margin: 0 0 5px;
  padding: 0;
  width: 327px;

  font-size: 22px;
  font-weight: 700;
  line-height: 1.2;

  letter-spacing: 0.03em;
}

.gallery__inner b {
  display: block;

  margin-bottom: 12px;

  font-size: 28px;
  font-weight: 600;
  line-height: 1.3;

  letter-spacing: 0.03em;
  text-transform: uppercase;
}

.gallery__size-list {
  display: flex;
  flex-wrap: wrap;

  margin-bottom: 11px;
}

.gallery__size-item {
  margin-right: 7px;
  padding: 10px 3px;
  width: 56px;
  height: 47px;

  border-radius: 2px;
  background: rgba(235, 234, 234, 0.7);

  &--curent {
    background: $color-lightgreen;
  }
}

.gallery__inner span {
  display: block;

  margin-bottom: 20px;

  font-size: 10px;
  font-weight: 700;
  line-height: 1.5;
}

.gallery .gallery__size-item span {
  display: block;

  margin: 0;

  font-size: 12px;
  font-weight: 600;
  line-height: 1.3;

  text-align: center;
  text-align: center;
  letter-spacing: 0.02em;
  text-transform: uppercase;

  &:nth-child(2n) {
    color: $color-gray-light;
  }
}

.gallery__text {
  margin-bottom: 25px;
}

.gallery .gallery__text p {
  margin-bottom: 0;
  padding-right: 94px;
}

.gallery .gallery__text span {
  margin-bottom: 0;
  padding-right: 35px;
}

.gallery__inner .gallery__text p,
.gallery__inner .gallery__text span {
  font-family: Georgia;
  font-size: 18px;
  font-weight: normal;
  font-style: normal;
  line-height: 1.5;

  letter-spacing: 0.01em;
  text-transform: none;

  color: rgba(0, 0, 0, 0.8);
}

.gallery__info-img {
  margin-bottom: 58px;
}

.gallery__btn {
  display: inline-block;

  margin-bottom: 15px;
  padding: 10px 5px;
  width: 185px;

  font-size: 12px;
  font-weight: 600;
  line-height: 1.3;

  transform: none;
  text-align: center;
  letter-spacing: 0.02em;
  text-transform: uppercase;

  border: 1px solid $color-default-black;
  border-radius: 2px;

  &--green {
    margin-right: 7px;

    border: 0;
    background-color: $color-lightgreen;
  }
}

.gallery__info-text {
  margin-bottom: 17px;
  width: 209px;
}

.gallery .gallery__info-text p {
  position: relative;

  padding: 5px;
  padding-left: 35px;

  font-size: 12px;
  line-height: 12px;

  letter-spacing: 0.01em;

  opacity: 1;

  svg {
    position: absolute;
    top: 50%;
    left: 4px;

    transform: translateY(-50%);
  }

  &:first-of-type {
    margin-bottom: 10px;

    border-radius: 17px;
    background: rgba(66, 240, 129, 0.25);
  }
}

.gallery__links {
  display: flex;
  flex-wrap: wrap;

  margin-bottom: 35px;

  width: 352px;
}

.gallery__links-item {
  margin-right: 15px;
  width: 120px;

  &--last {
    width: 167px;
  }
}

.gallery__links-item a {
  display: block;

  font-size: 10px;
  font-weight: 700;
  line-height: 1.5;

  opacity: 0.5;
  color: $color-default-black;

  &:hover {
    text-decoration: underline;
  }
}
