.call {
  position: relative;
  overflow: hidden;

  font-family: $font-family--open-sans;
  font-weight: 600;
  font-size: 12px;
  line-height: 128%;

  perspective: 500px;
  transform-style: preserve-3d;
  transform-origin: left top;

  &--author {
    min-height: 120px;
    background-color: $color-purp;

    padding-top: 21px;
    padding-left: calc(50% + 30px);
    padding-right: 20px;

    .call__img {
      position: absolute;
      top: 15px;
      right: calc(50% - 18px);
    }

    .call__text {
      margin: 0;
      margin-bottom: 13px;
    }
  }

  &--cert {
    padding-bottom: 11px;
    min-height: 160px;
    background-color: $color-lightgreen;
    background-clip: content-box;

    .call__img {
      position: absolute;
      top: 6px;
      left: calc(50% - 152px);
    }

    .call__btn {
      position: absolute;
      left: calc(50% + 55px);
      bottom: 20px;
    }
  }

  &--slide {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;

    padding-left: 32px;
    padding-bottom: 32px;

    width: 100%;
    min-height: 240px;

    background-color: #586a76;
    background-image: url("/img/bg/promo-slide-sale@1x.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: 375px 240px;

    .call__strong {
      display: block;
      margin-top: 10px;

      font-size: 110px;
      line-height: 1;
      font-weight: 800;
      color: $color-lightblue;

      text-transform: uppercase;
      transform: rotateX(-5deg) rotateY(45deg);
    }

    .call__text {
      margin: 0;
      max-width: 150px;

      font-weight: 700;
      font-size: 14px;
      line-height: 18px;

      letter-spacing: 0.03em;

      color: $color-default-white;
    }

    .call__btn {
      display: inline-block;
    }
  }
}

@media (min-width: $viewport--desktop) {
  .call {
    &--cert {
      display: none;
    }

    &--slide {
      min-height: 340px;

      .call__strong {
        margin-top: 55px;
      }
    }
  }
}
