.main-nav__logo {
  display: block;
}

.main-nav__logo-img {
  &--sym-white {
    display: none;
  }
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.main-nav__item {
  &.is-expanded {
    .main-nav__link {
      color: $color-lightgreen;
    }

    .main-nav__icon {
      fill: $color-lightgreen;
    }

    .main-nav__sublist {
      display: block;
      padding-top: 18px;
    }

    .main-nav__subitem + .main-nav__subitem {
      margin-top: 6px;
    }

    .main-nav__sublink {
      font-size: 17px;
      line-height: 180%;

      &--sale {
        &::after {
          display: inline-block;
          vertical-align: middle;
          margin-left: 6px;
          padding: 0 4px;
          font-size: 8px;
          line-height: 12px;
          font-weight: 700;
          letter-spacing: 0.01em;
          text-transform: uppercase;
          color: $color-default-black;
          border-radius: 1px;
          background-color: $color-orange;

          content: 'sale';
        }
      }
    }
  }
}

.main-nav__sublist {
  display: none;

  margin: 0;
  padding: 0;
  list-style: none;
}

.main-nav__sale {
  display: none;
}

.main-nav__close-btn {
  display: none;
}

@media (min-width: $viewport--desktop) {
  .main-nav.is-main {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap: wrap;

    color: $color-default-black;

    .main-nav__logo {
      order: 1;
      margin-right: calc(100% - 155px);
      margin-bottom: -62px;
    }

    .main-nav__logo-img {
      width: 155px;
      height: 62px;
    }

    .main-nav__list {
      order: 4;

      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-wrap: wrap;

      margin-left: 205px;
      // 165px - расстояние (gap) между списком и правой границей блока навигации
      // 20px - суммарный внешний отступ слева и справа от элемента списка
      flex: 0 0 calc(100% - 205px - 165px + 20px);

      font-weight: 600;
      font-size: 12px;
      line-height: 15px;

      letter-spacing: 0.02em;
      text-transform: uppercase;
    }

    .main-nav__list--major {
      order: 2;

      margin-top: 10px;
      margin-bottom: 7px;

      font-size: 16px;
      line-height: 28px;
      letter-spacing: 0.03em;
    }

    .main-nav__list--extra {
      display: none;
    }

    .main-nav__item {
      padding: 0 10px;

      &.is-expanded {
        position: relative;

        background-color: $color-default-black;

        .main-nav__link {
          color: $color-lightgreen;
        }

        .main-nav__sublist {
          position: absolute;
          top: 100%;
          left: 0;
          right: 0;
          z-index: 10;

          display: block;
          padding: 18px 10px 30px;

          min-width: 170px;

          background-color: $color-default-black;
        }

        .main-nav__sublink {
          font-weight: 600;

          letter-spacing: 0.02em;
          text-transform: none;

          color: $color-default-white;
        }
      }
    }

    .main-nav__icon {
      display: none;
    }

    .main-nav__sale {
      order: 3;

      display: block;
      margin-top: 14px;
      margin-left: 18px;

      padding: 0 11px;

      font-weight: 700;
      font-size: 16px;
      line-height: 19px;

      letter-spacing: 0.03em;
      text-transform: uppercase;

      background-color: $color-default-white;
      border-radius: 4px;
    }
  }

  .main-nav.is-open:not(.is-main) {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap: wrap;
    align-content: flex-start;

    .main-nav__logo {
      margin-bottom: 69px;
      margin-left: calc(100% - 107px - 20px);
      transform: translateX(50%);
    }

    .main-nav__logo-img--sym-white {
      display: block;
      width: 107px;
    }

    .main-nav__list {
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      align-items: flex-start;

      flex: 1 0 45%;
    }

    .main-nav__list--major {
      flex-basis: 55%;
    }

    .main-nav__list--extra {
      display: none;
    }

    .main-nav__item {
      padding-left: 75px;

      &.is-expanded {
        .main-nav__sublist {
          padding-top: 8px;
        }
      }
    }

    .main-nav__link,
    .main-nav__sublink {
      font-weight: 700;
      font-size: 20px;
      line-height: 280%;

      color: $color-lightgreen;
    }

    .main-nav__sublink {
      color: $color-default-white;
    }

    .main-nav__sale {
      display: block;
      margin-top: 15px;
      margin-left: calc(55% + 75px);
      padding: 0 10px;

      font-size: 21px;
      font-weight: 700;
      line-height: 31px;
      letter-spacing: 0.01em;
      text-transform: uppercase;

      color: $color-default-black;

      background-color: $color-orange;
      border-radius: 6px;
    }

    .main-nav__icon {
      margin-left: 10px;
      width: 9px;
      height: auto;
      fill: $color-default-white;
    }

    .main-nav__close-btn {
      order: -1;
      display: block;
      margin-top: 10px;
    }

    .main-nav__close-icon {
      width: 20px;
      fill: $color-default-white;
    }
  }
}

// Меню раскрывается по нажатию на гамбургер только на мобильной версии
// На десктопе "раскрывается" весь заголовок
@media (max-width: $viewport--desktop - 1) {
  .main-nav {
    &.is-open {
      display: flex;
      flex-direction: column;
      align-items: flex-start;

      position: absolute;
      top: 0;
      left: 0;
      z-index: 3;

      padding-top: 24px;
      padding-bottom: 36px;
      padding-left: 28px;
      padding-right: 28px;

      width: 322px;

      font-family: $font-family--open-sans;
      font-weight: 600;
      font-size: 19px;
      line-height: 160%;

      letter-spacing: 0.02em;

      color: $color-default-white;

      background-color: transparent;

      &::before,
      &::after {
        position: absolute;
        left: 0;
        right: 0;
        z-index: -1;
        background-color: $color-default-black;
        content: '';
      }

      &::before {
        top: 0;
        bottom: 109px;

        transform: skewX(10deg);
        transform-origin: left bottom;
      }

      &::after {
        bottom: 0;

        height: 109px;

        transform: skewX(-32deg);
        transform-origin: left top;
      }

      .main-nav__logo {
        display: none;
      }

      .main-nav__list {
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        max-width: 156px;

        &--extra {
          max-width: 100%;

          text-transform: uppercase;
          color: $color-lightgreen;

          .main-nav__item {
            margin: 9px 0;
          }
        }
      }

      .main-nav__item {
        margin: 13px 0;

        &.is-shaded:not(.main-nav__item--sale) {
          opacity: 0.5;
          color: $color-default-white;
        }
      }

      .main-nav__link,
      .main-nav__sublink {
        color: inherit;
      }

      .main-nav__sale {
        order: -1;
        display: block;
        margin-bottom: 12px;
        padding: 0 13px;

        font-size: 22px;
        line-height: 34px;
        font-weight: 700;
        text-transform: uppercase;
        color: $color-default-black;

        background-color: $color-orange;
      }

      .main-nav__icon {
        margin-left: 4px;
        opacity: 0.75;
        fill: $color-default-white;
      }

      .main-nav__close-btn {
        order: -2;
        display: block;
        margin-bottom: 38px;
      }

      .main-nav__close-icon {
        fill: $color-default-white;
      }
    }
  }
}
