.close {
  position: relative;
  width: 20px;
  height: 20px;

  cursor: pointer;

  border: 0;
  background-color: transparent;
  transition: opacity $default-transition-settings;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;

    width: 18px;
    height: 2px;

    background-color: $color-default-black;
  }

  &::before {
    transform: translate(-50%, -50%) rotate(45deg);
  }

  &::after {
    transform: translate(-50%, -50%) rotate(-45deg);
  }

  &:hover,
  &:focus {
    opacity: 0.4;
  }
}

.cart .close {
  @media (max-width: $viewport--tablet) {
    display: none;
  }
}

.popup-favourites .close {
  margin-bottom: 20px;
}
