@media (min-width: $viewport--desktop) {
  .about {
    position: relative;

    padding-top: 84px;
    padding-bottom: 147px;

    text-align: left;

    background-image: url("/img/svg/deco-about-desktop.svg"), url("/img/bg/about-model-desktop@1x.jpg");
    background-position: center bottom -55px, center top;
    background-repeat: repeat-x, repeat-x;
    background-size: 100% auto, 1280px 786px;

    &--mobile {
      display: none;
    }

    &--desktop {
      display: block;
    }
  }

  .about__wrapper {
    position: relative;
    z-index: 1;

    perspective: 105px;
    perspective-origin: 0 23%;
    transform-style: preserve-3d;
  }

  .about__slogan {
    margin: 0;
    margin-bottom: 32px;

    font-size: 120px;
    font-weight: 800;
    line-height: 90%;
    text-transform: uppercase;
    color: $color-default-black;

    transform: translateX(-7px) rotateY(5deg);
    transform-origin: left 50%;
  }

  .about__slogan-img {
    margin-bottom: 31px;
  }

  .about__text {
    margin: 0;
    margin-bottom: 40px;
    width: 540px;

    font-family: $font-family--georgia;
    font-size: 18px;
    line-height: 141%;
  }

  .about__btn {
    display: inline-block;
    margin-bottom: 35px;
  }

  .about__social-label {
    display: block;
    margin-bottom: 7px;

    font-weight: 700;
    font-size: 10px;
    line-height: 150%;
  }
}

@media (max-width: $viewport--desktop - 1) {
  .about {
    position: relative;

    padding-top: 89px;
    padding-bottom: 47px;

    font-family: $font-family--open-sans;
    text-align: center;

    background-color: $color-about-gray;

    background-image: url("/img/bg/about-model-mobile@1x.jpg"), url("/img/bg/about-line-mobile@1x.jpg");
    background-repeat: no-repeat, repeat-x;
    background-position: top -31px right, top -31px right;
    background-size: 375px 490px, 2px 490px;

    // Эффект затемнения
    &::before {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;

      background-color: rgba(0, 0, 0, 0.2);
      content: "";
    }

    &--mobile {
      display: block;
    }

    &--desktop {
      display: none;
    }
  }

  .about__wrapper {
    position: relative;
    z-index: 1;
    padding: 0 33px;
  }

  .about__logo {
    margin-bottom: 98px;
  }

  .about__slogan {
    margin-bottom: 15px;

    color: $color-default-white;
    font-size: 44px;
    line-height: 46px;
    font-weight: 700;
  }

  .about__btn {
    display: inline-block;
  }
}
