.footer {
  margin-top: auto;

  background-color: $color-black;
}

.footer a {
  transition: opacity $default-transition-settings;

  &:hover,
  &:focus {
    opacity: 0.5;
    outline: 0;
  }
}

.footer__inner {
  margin: 0 auto;
  padding: 35px 10px 62px;
  width: 83.9%;
  line-height: 1.3;

  @media (max-width: $viewport--desktop) {
    padding: 50px 40px 100px;
    width: auto;
  }

  @media (max-width: $viewport--tablet) {
    padding: 9px 23px 65px;
  }
}

.footer__wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  margin: 0 auto 11px;

  @media (max-width: $viewport--desktop) {
    margin-bottom: 30px;
  }

  @media (max-width: $viewport--tablet) {
    display: none;
  }
}

.footer__header,
.footer__header-link {
  margin: 0 0 15px;
  padding: 0;

  font-size: 14px;
  font-weight: 600;

  letter-spacing: 0.05em;
  text-transform: uppercase;

  color: $color-lightgreen;
}

.footer__logo {
  order: 1;

  cursor: pointer;
  transform: translateX(8.5%);

  @media (max-width: $viewport--desktop) {
    order: -1;

    margin-bottom: 45px;
    width: 100%;

    transform: none;
  }
}

.footer__logo img {
  width: 210px;

  @media (max-width: $viewport--tablet) {
    position: relative;
    left: 50%;

    transform: translateX(-50%);
  }
}

.footer__item {
  padding-top: 5px;

  &--about-us {
    width: 105px;
  }

  &--cooperation {
    width: 150px;

    transform: translateX(8.3%);

    @media (max-width: $viewport--desktop) {
      margin-bottom: 20px;

      transform: none;
    }
  }

  &--buyers {
    order: 2;

    width: 175px;

    transform: translateX(14%);

    @media (max-width: $viewport--desktop) {
      transform: none;
    }
  }

  &--contacts {
    order: 3;

    width: 95px;

    transform: translateX(5%);

    @media (max-width: $viewport--desktop) {
      transform: none;
    }
  }
}

.footer__list li {
  margin-bottom: 10px;

  @media (max-width: $viewport--desktop) {
    margin-bottom: 15px;
  }
}

.footer__list a {
  display: block;

  font-size: 14px;
  line-height: 1.3;

  text-decoration: none;
  letter-spacing: 0.1em;

  color: $color-default-white;
}

.footer__social {
  padding-top: 13px;

  @media (max-width: $viewport--tablet) {
    padding: 15px 0 0 18px;
  }
}

.footer__inner p {
  margin: 0;
  padding: 0;

  font-size: 12px;

  letter-spacing: 0.07em;

  color: $color-default-white;

  @media (max-width: $viewport--tablet) {
    display: none;
  }
}

.footer__link-copyright {
  display: inline-block;

  vertical-align: top;
  text-transform: uppercase;

  color: $color-default-white;
}

.footer__wrap-sm {
  display: none;

  @media (max-width: $viewport--tablet) {
    display: block;

    margin-bottom: 20px;
  }
}

.footer__wrap-sm .footer__list {
  margin-bottom: 20px;
}

.footer__wrap-sm .footer__list li {
  margin-bottom: 0;
}

.footer__wrap-sm .footer__list a {
  padding: 18px 20px 20px 16px;
  width: auto;

  font-size: 12px;

  text-transform: uppercase;

  border-bottom: 1px solid $color-gray-dark;
}

.footer__list button {
  position: relative;

  display: block;
  overflow: hidden;

  box-sizing: border-box;
  margin: 0;
  padding: 17px 30px 18px 16px;
  width: 100%;
  max-width: 100%;

  font-family: inherit;
  font-size: 14px;
  line-height: 1.3;

  cursor: pointer;
  user-select: none;
  text-align: left;
  text-decoration: none;
  text-transform: uppercase;
  text-overflow: ellipsis;

  color: $color-default-white;
  border: none;
  border-bottom: 1px solid $color-gray-dark;
  outline: none;
  background-color: transparent;

  svg {
    position: absolute;
    top: 20px;
    right: 10px;

    width: 7px;
    height: 9px;
  }

  &:hover,
  &:focus {
    opacity: 0.5;
  }
}

.footer__contacts {
  padding-left: 10px;

  font-size: 12px;
  line-height: 1.7;

  @media (max-width: $viewport--tablet) {
    margin-bottom: 25px;
    padding-left: 18px;
  }
}

.footer__contacts p {
  display: block;

  margin: 0 0 27px;
  padding: 0;

  letter-spacing: 0.1em;
  text-transform: uppercase;

  color: $color-default-white;
}

.footer__contacts-list p {
  margin-bottom: 4px;

  letter-spacing: 0.03em;
  text-transform: none;
}

.footer__contacts-list a {
  display: block;

  font-size: 12px;
  font-weight: 700;
  line-height: 1.5;

  text-decoration: none;
  letter-spacing: 0.1em;

  color: $color-default-white;
}

.footer--hidden {
  @media (max-width: $viewport--tablet) {
    display: none;
  }
}
