.result {
  align-self: flex-start;

  padding: 25px 15px 20px 46px;
  width: 38.1%;

  background-color: $color-lightgreen;

  @media (max-width: $viewport--desktop) {
    padding-right: 46px;
    width: 100%;
  }

  @media (max-width: $viewport--tablet) {
    order: 3;

    padding: 9px 0 0;

    background-color: transparent;
  }
}

.result__container {
  flex-grow: 2;

  width: 253px;

  @media (max-width: $viewport--desktop) {
    width: 100%;
  }
}

.result__container p {
  margin: 0;
  padding: 0;

  font-size: 10px;
  line-height: 1.3;

  letter-spacing: 0.01em;

  opacity: 0.5;

  @media (max-width: $viewport--tablet) {
    padding: 0 30px 0 0;

    font-size: 10px;
    line-height: 1.3;

    letter-spacing: 0.01em;

    opacity: 0.5;
    color: $color-default-black;
  }
}

.result__wrap {
  display: flex;
}

.result__info {
  padding-top: 23px;

  @media (max-width: $viewport--tablet) {
    padding: 23px 0 0 15px;
  }
}

.result__info p {
  margin: 0;
  padding: 0;

  font-size: 26px;
  font-weight: 700;
  line-height: 26px;

  letter-spacing: 0.02em;
  text-transform: none;

  opacity: 1;

  @media (max-width: $viewport--tablet) {
    font-size: 20px;
    line-height: 20px;
  }
}

.result__info span {
  font-size: 10px;
  line-height: 12px;

  letter-spacing: 0.02em;

  opacity: 0.5;

  @media (max-width: $viewport--tablet) {
    display: none;
  }
}

.result__price {
  margin-left: auto;
}

.result__price b {
  font-size: 30px;
  font-weight: 700;
  line-height: 30px;

  letter-spacing: 0.02em;
}

.result__gift {
  position: relative;

  margin: 0 0 17px auto;
  width: 130px;

  svg {
    position: absolute;
    top: 0;
    left: 0;

    width: 10px;
    height: 12px;
  }

  @media (max-width: $viewport--tablet) {
    svg {
      display: none;
    }
  }
}

.result__gift p {
  margin: 0;
  padding: 0 0 0 15px;

  font-size: 10px;
  font-weight: 600;
  line-height: 1.1;

  text-align: right;
  letter-spacing: 0.02em;
  text-transform: uppercase;

  opacity: 1;

  @media (max-width: $viewport--tablet) {
    display: none;
  }
}

.result__discount {
  display: flex;
  flex-wrap: wrap;
  align-items: center;

  margin-bottom: 35px;

  @media (max-width: $viewport--tablet) {
    margin-bottom: 32px;
  }
}

.result__discount span {
  font-size: 12px;
  font-weight: 600;
  line-height: 14px;

  letter-spacing: 0.02em;

  @media (max-width: $viewport--tablet) {
    padding: 6px 0 0 15px;
  }
}

.result__discount b {
  margin-left: auto;
  padding: 2px 12px 2px 7px;

  font-weight: 400;

  text-transform: uppercase;

  background-color: $color-default-white;

  @media (max-width: $viewport--tablet) {
    margin-right: 15px;
    padding: 2px 5px;

    border-radius: 2px;
    background-color: $color-orange;
  }
}

.result input {
  display: inline-block;

  margin: 0 0 16px;
  padding: 13px 15px 16px;
  width: 100%;

  font-size: 11px;
  line-height: 1.3;

  cursor: pointer;
  vertical-align: top;
  letter-spacing: 0.08em;

  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 0;
  outline: 0;
  background-color: $color-default-white;

  appearance: none;

  @media (max-width: $viewport--tablet) {
    background-color: transparent;
  }
}

.result__btn {
  margin-bottom: 12px;
  padding: 9px;
  width: 100%;

  font-size: 12px;
  font-weight: 600;

  transition: opacity $default-transition-settings;
  letter-spacing: 0.02em;
  text-transform: uppercase;

  color: $color-default-white;
  background-color: $color-default-black;

  @media (max-width: $viewport--tablet) {
    background-color: $color-lightgreen;
  }
}

.result__text-wrap p {
  margin: 0;
  padding: 0 22px 0 0;

  font-size: 10px;
  font-weight: normal;

  letter-spacing: 0.01em;
  text-transform: none;

  opacity: 0.5;
  color: $color-default-black;

  @media (max-width: $viewport--tablet) {
    padding-right: 45px;
  }
}

.result__text-wrap p:first-of-type {
  display: block;

  @media (max-width: $viewport--tablet) {
    display: none;
  }
}

.result__text-wrap p:last-of-type {
  display: none;

  @media (max-width: $viewport--tablet) {
    display: block;
  }
}

.result__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  margin-bottom: 14px;
  padding-bottom: 5px;

  border-bottom: 1px dashed $color-default-black;
}

.result__list dt {
  margin: auto 0 22px 0;
  width: 148px;

  font-size: 18px;
  line-height: 1.2;

  letter-spacing: 0.01em;

  @media (max-width: $viewport--desktop) {
    width: 80%;
  }
}

.result__list dd {
  display: block;
  align-self: flex-start;

  margin: 0;

  font-size: 18px;
  font-weight: 700;
  line-height: 19px;

  @media (max-width: $viewport--tablet) {
    font-size: 16px;
    font-weight: 600;
    line-height: 18px;

    letter-spacing: 0.02em;
  }
}

.result__wrap-order {
  display: flex;
  justify-content: space-between;

  margin: 0 0 5px;
}

.result__wrap-order dt {
  margin: auto 0 0;

  font-size: 18px;
  font-weight: 700;
}

.result__wrap-order dd {
  font-size: 30px;
  font-weight: 700;
  line-height: 1.15;

  letter-spacing: 0.02em;
}

.result__gift-order {
  margin-bottom: 32px;
}

.result__link-order {
  display: block;

  margin-bottom: 5px;

  font-size: 10px;
  line-height: 1.5;

  transition: opacity $default-transition-settings;
  text-decoration: underline;

  color: $color-default-black;
  outline: 0;

  &:hover,
  &:focus {
    text-decoration: none;

    opacity: 0.7;
  }
}

.order .result {
  padding: 45px 15px 35px 58px;

  @media (max-width: $viewport--desktop) {
    position: absolute;
    bottom: 75px;
    left: 0;

    padding: 50px;

    background-color: $color-lightgreen;
  }

  @media (max-width: $viewport--tablet) {
    bottom: 18px;

    padding: 20px 26px;
  }
}

.order .result__container {
  width: 231px;

  @media (max-width: $viewport--desktop) {
    width: auto;
  }
}

.order__inner .result__gift-order p {
  margin-left: 135px;

  font-size: 12px;
  line-height: 16px;

  letter-spacing: 0.01em;
  text-transform: uppercase;

  @media (max-width: $viewport--desktop) {
    display: block;

    margin: 0;
    padding-right: 10px;
    width: auto;

    text-align: right;
  }
}

.order .result__wrap-order p {
  margin-top: 12px;

  font-size: 20px;
  font-weight: 700;
  line-height: 20px;
}

.order__result .result__price-wrap {
  margin-top: -15px;
}

.result .result__discount {
  margin-right: -6px;
  padding: 3px 7px;

  font-size: 14px;

  text-align: center;

  border-radius: 2px;
  background-color: $color-default-white;
}

.cart .result .result__discount {
  margin: 0 0 32px auto;
  padding: 0;

  font-size: 16px;
  line-height: 1.3;

  background-color: $color-transparent;
}
