.cart {
  overflow: hidden;

  margin-left: auto;
  padding: 73px 10px 0 0;
  width: 87.6%;

  line-height: 1.3;

  @media (max-width: $viewport--desktop) {
    margin: 0 auto 40px;
    padding: 73px 23px 30px;
    width: 100%;
  }

  @media (max-width: $viewport--tablet) {
    margin-bottom: 0;
    padding: 22px 20px 30px;
  }

  @media (max-width: $viewport--sm) {
    padding: 22px 10px 30px;
  }
}

.cart h2 {
  margin: 0 0 12px;
  padding: 0;

  font-size: 28px;
  font-weight: 600;

  letter-spacing: 0.03em;
  text-transform: uppercase;

  @media (max-width: $viewport--tablet) {
    margin: 0 0 15px 5px;

    font-size: 16px;
    font-weight: 700;

    text-transform: none;
  }
}

.cart__inner {
  display: flex;
  flex-wrap: wrap;

  @media (max-width: $viewport--desktop) {
    flex-direction: column;
  }
}

.cart__list {
  margin-bottom: 35px;
  padding-top: 35px;
  width: 55.3%;

  @media (max-width: $viewport--desktop) {
    padding-bottom: 30px;
    width: 100%;
  }

  @media (max-width: $viewport--tablet) {
    margin-bottom: 15px;
    padding: 0 5px;
  }

  @media (max-width: $viewport--sm) {
    padding: 0;
  }
}

.cart__item {
  position: relative;

  margin-bottom: 68px;
  width: 100%;

  @media (max-width: $viewport--tablet) {
    margin-bottom: 42px;
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.cart .cart__close {
  position: absolute;
  top: 0;
  right: 5px;

  @media (max-width: $viewport--tablet) {
    display: none;
  }
}

.cart__btn-open {
  display: none;

  @media (max-width: $viewport--tablet) {
    position: absolute;
    top: -7px;
    right: 11px;

    display: block;

    padding: 0;
  }

  @media (max-width: $viewport--sm) {
    right: 0;
  }
}

.cart__wrap {
  display: flex;
  justify-content: space-between;

  @media (max-width: $viewport--tablet) {
    margin-bottom: 7px;
  }
}

.cart__description-wrap {
  order: 1;

  width: 67.6%;

  @media (max-width: $viewport--desktop) {
    width: 100%;
  }
}

.cart__description-wrap h3 {
  margin: 0 0 9px;
  padding: 0 40px 0 0;

  font-size: 22px;
  font-weight: 700;
  line-height: 1.2;

  letter-spacing: 0.03em;

  @media (max-width: $viewport--desktop) {
    width: 100%;
  }

  @media (max-width: $viewport--tablet) {
    margin-bottom: 5px;
    padding: 3px 45px 0 0;

    font-size: 12px;
    line-height: 15px;
  }

  @media (max-width: $viewport--sm) {
    padding: 3px 30px 0 0;

    font-size: 11px;
  }
}

.cart__description-wrap p {
  margin: 0 0 14px;
  padding: 0;

  font-size: 12px;

  letter-spacing: 0.05em;
  text-transform: uppercase;

  color: $color-gray;

  @media (max-width: $viewport--tablet) {
    display: none;
  }
}

.cart__description {
  display: flex;

  margin-bottom: 10px;
  padding-right: 10px;
}

.cart__description b {
  margin-left: auto;

  font-size: 28px;
  font-weight: 600;
  line-height: 1.2;

  letter-spacing: 0.03em;
  text-transform: uppercase;

  @media (max-width: $viewport--tablet) {
    margin-top: 20px;

    font-size: 16px;
  }

  svg {
    @media (max-width: $viewport--tablet) {
      width: 10px;
      height: 11px;
    }
  }
}

.cart__img-wrap {
  flex-shrink: 0;

  margin-right: 25px;

  @media (max-width: $viewport--desktop) {
    margin-right: 30px;
  }

  @media (max-width: $viewport--tablet) {
    margin-right: 14px;
  }

  @media (max-width: $viewport--sm) {
    margin-right: 10px;
  }
}

.cart__img-wrap img {
  width: 168px;
  height: 100%;

  @media (max-width: $viewport--tablet) {
    width: 91px;
  }
}

.cart__price-wrap {
  margin: -20px 0 0 auto;

  @media (max-width: $viewport--tablet) {
    margin-top: 0;
  }
}

.cart__price-wrap b {
  display: block;

  padding-right: 0;
}

.cart__description .cart__price-wrap {
  @media (max-width: $viewport--tablet) {
    margin-top: 10px;
    width: 50px;
  }
}

.cart__result {
  margin: 18px -15px 0 auto;

  @media (max-width: $viewport--desktop) {
    order: 1;

    margin: 0;
    padding: 0;
  }
}
